<template lang="pug">
  header.hd-bar.after 
    slot.sec-l( name="sec-l" )
      section.headimg
        img( src="../images/touxiang.jpg" )
    section.bar-title 
      slot
        span {{titleName}}

    section.operation-r 
      slot( name="sec-r") 
</template>
<script>
export default {
  data() {
    return {

    }
  },
  props: {
    titleName: {
      type: String
    }
  }
}
</script>
<style lang="stylus" scoped>
  .hd-bar 
    box-sizing border-box
    z-index 50
    width 100%
    height $hd-bar-height
    top 0 
    left 0
    background-image linear-gradient(20deg,#449afd,#28c3fc)
    display flex 
    align-items center
    padding 0 .75rem 
    position fixed
    justify-content space-between
    color #ffffff
    .headimg 
      width 2.35rem 
      height 2.35rem
      border-radius 50%
      background green
      img 
        width 100% 
        height 100% 
        outline none
        border-radius 50% 
    .bar-title 
      position absolute
      text-align center
      top 50% 
      left 50% 
      transform translate3d(-50%, -50%, 0)
      font-size 16px
      color #ffffff
    .operation-r  
      color #ffffff
      font-size 16px  


          

</style>





